import React from "react";
import styled from "styled-components";

const WorkHistoryProps = ({
  title,
  text1,
  text2,
  text3,
  position,
  company,
  location,
  img,
}) => {
  return (
    <div>
      <Container>
        <Wrapper>
          <Image src={img} />
          <Content>
            <Title>{title}</Title>
            <Company>{company}</Company>
            <Position>{position}</Position>
            <Location>{location}</Location>
            <SubText>
              {text1}
              <br />
              <br />
              {text2}
              <br />
              <br />
              {text3}
            </SubText>
          </Content>
        </Wrapper>
      </Container>
    </div>
  );
};

export default WorkHistoryProps;

const Company = styled.div`
  margin-bottom: 10px;
`;

const Position = styled.div`
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 20px;
`;

const Location = styled.div`
  margin-bottom: 10px;
  font-weight: bold;
`;

const SubText = styled.div`
  margin-bottom: 5px;
  width: 600px;

  @media screen and (max-width: 760px) {
    width: 90%;
`;

const Content = styled.div`
  @media screen and (max-width: 760px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;

const Title = styled.div`
  font-size: 38px;
  margin-bottom: 10px;
  font-weight: bold;
  align-items: center;
  width: 600px;
  color: #3377cc;
  :hover {
    cursor: pointer;
    color: #0080e5;
  }
`;

const Image = styled.img`
  width: 150px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
  background-color: red;
  margin-right: 30px;

  @media screen and (max-width: 760px) {
    width: 90%;
    height: 300px;
    margin: 30px 0;
    object-fit: cover;
    border-radius: 10px;
`;

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;

  @media screen and (max-width: 760px) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;

const Container = styled.div`
  width: 100%;
  height: 100%;
  color: black;
`;
