import React from "react";
import styled from "styled-components";

const Hero = () => {
  return (
    <div>
      <Container>
        <Wrapper>
          <SubTitle> Hello,</SubTitle>
          <Title>I am Engr Bosede</Title>
        </Wrapper>
      </Container>
    </div>
  );
};

export default Hero;

const SubTitle = styled.div`
  font-size: 36px;
`;

const Title = styled.div`
  font-size: 36px;
  font-weight: bold;
`;

const Wrapper = styled.div`
  width: 90%;
  height: 90vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  flex-direction: column;
`;

const Container = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url("Assets/ment4.jpg");
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
`;
