import React from "react";
import styled from "styled-components";

const VolunteeringCard = ({ text, marbot, title, subtitle }) => {
  return (
    <div>
      <Container>
        <Wrapper>
          <Title>{title}</Title>
          <SubTitle>
            {subtitle}
            <Span marbot={marbot}>{text}</Span>
          </SubTitle>
        </Wrapper>
      </Container>
    </div>
  );
};

export default VolunteeringCard;

const SubTitle = styled.div`
  font-size: 20px;
  margin: 0 10px;
`;

const Span = styled.div`
  margin-bottom: ${({ marbot }) => marbot};
  font-weight: bold;
`;

const Title = styled.div`
  font-size: 28px;
  font-weight: bold;
  margin: 0 10px;
`;

const Wrapper = styled.div`
  width: 300px;
  height: 100%;
  display: flex;
  color: black;
  border: 2px solid silver;
  flex-direction: column;
  padding: 20px 0;
  align-items: flex-start;

  :hover {
    transform: scale(1.02);
    cursor: pointer;
  }
`;

const Container = styled.div`
  width: 300px
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  margin: 10px;
`;
