import React from "react";
import styled from "styled-components";
import { TiSocialLinkedin } from "react-icons/ti";
import { TiSocialFacebook } from "react-icons/ti";

const Footer = () => {
  return (
    <div>
      <Container>
        <Wrapper>
          <Contact>
            <Title>Connect with Engr Bosede</Title>
            <SubTitle>
              Experienced Senior Instrumentation Control Engineer with a
              demonstrated history of working in the oil and energy industry.
              Skilled in Engineering, Procurement, Business and Project
              Management Strategy
            </SubTitle>
            <Item>
              <Logo1 href="https://www.linkedin.com/in/bosede-oyekunle-r-engr-97bb6124">
                <Icon1 />
              </Logo1>
              <Logo2 href="https://www.facebook.com/oyekunle.bosede">
                <Icon2 />
              </Logo2>
            </Item>
          </Contact>
        </Wrapper>
      </Container>
    </div>
  );
};

export default Footer;

const Icon1 = styled(TiSocialLinkedin)`
  color: white;
  font-size: 100px;

  :hover {
    cursor: pointer;
    transform: scale(1.08);
  }
`;

const Icon2 = styled(TiSocialFacebook)`
  color: white;
  font-size: 100px;

  :hover {
    cursor: pointer;
    transform: scale(1.08);
  }
`;

const Item = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const Title = styled.div`
  color: white;
  font-weight: bold;
  font-size: 28px;
`;
const SubTitle = styled.div`
  color: white;
  font-size: 20px;
  margin: 10px;
`;

const Logo1 = styled.a`
  width: 200px;
  height: 200px;
`;

const Logo2 = styled.a`
  width: 200px;
  height: 200px;
`;

const Contact = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Wrapper = styled.div`
  width: 100%;
  height: 300px;
  margin: 20px;
  justify-content: center;
  display: flex;
`;

const Container = styled.div`
  width: 100%;
  height: 100%;
  background-color: black;
  display: flex;
  justify-content: center;
`;
