import React from "react";
import styled from "styled-components";
import VolunteeringCard from "./VolunteeringCard";

const Volunteering = () => {
  return (
    <div>
      <Container>
        <VolunteeringCard
          title="TechWomen"
          subtitle="Mentored young women to start and navigate their career in the Technology Industry."
          text="Mentor"
          marbot
        />

        <VolunteeringCard
          title="Pollinate Group"
          subtitle="Empowering and influencing more professional to participate in social business enterprise initiative for community development."
          text="Ambassador"
          marbot
        />

        <VolunteeringCard
          title="Career and Leadership Coach"
          subtitle="Support the initiative of nurturing and empowering young leaders (Undergraduates) in maximizing their potentials and become a positive global change agents."
          text="Impact Your World Leadership Initiative"
        />

        <VolunteeringCard
          title="Hadassah STEM"
          subtitle="Review concepts and programs aimed at encouraging more girls to take up a careeer in STEM in Africa.Trainings, Project, Career Workshops etc."
          text="Advocate"
          marbot
        />

        <VolunteeringCard
          title="Toast Master"
          subtitle="Coordinated the team of executives and over Thirty (30) members of DA Toastmasters, the Premier Corporate Toastmasters Club in Nigeria in achieving a Perfect 10 Educational and Leadership Goals and earned the President Distinguished Club Award."
          text="Impact Your World Leadership Initiative"
        />

        <VolunteeringCard
          title="The New York Academy of Sciences"
          subtitle="Review concepts and programs aimed at encouraging more girls to take up a careeer in STEM in Africa.Trainings, Project, Career Workshops etc."
          text="Worley Foundation Project Coordinator "
          marbot
        />
      </Container>
    </div>
  );
};

export default Volunteering;

const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;
