import React from "react";
import styled from "styled-components";

const Card = ({ image, title, text, title2, title3, slant, clr, rr }) => {
  return (
    <div>
      <Container>
        <Image src={image} />
        <Wrapper slant={slant}>
          <Title rr={rr}>
            {title}
            <Span clr={clr}>{text}</Span>
          </Title>
          <Title2>{title2}</Title2>
          <Title3>{title3}</Title3>
        </Wrapper>
      </Container>
    </div>
  );
};

export default Card;

const Title = styled.div`
  font-size: 30px;
  margin: 0;
  line-height: 1;
  font-weight: bold;
  display: flex;
  flex-direction: ${({ rr }) => (rr ? "row-reverse" : "row")};
`;

const Span = styled.div`
  color: ${({ clr }) => clr};
  margin: 0 5px;
`;

const Title2 = styled.div`
  font-size: 25px;
  margin: 0;
  line-height: 1;
  font weight: bold;
`;

const Title3 = styled.div`
  font-size: 20px;
  margin: 0;
  line-height: 1;
`;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  font-style: italic;
  /* font-style: ${({ slant }) => (slant ? "italic" : "normal")}; */
  position: absolute;
`;

const Image = styled.img`
  height: 100%;
  width: 100%;
  object-fit: cover;
`;

const Container = styled.div`
  width: 500px;
  height: 300px;
  background-color: cover;

  box-shadow: rgba(90, 90, 90, 0.2) 0px 2px 8px 0px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 350ms;
  transform: scale(1);
  margin: 10px;
  position: relative;

  :hover {
    transform: scale(1.03);
    cursor: pointer;
  }
`;
